為彌補審核制度的不足，並解決地面型光電設施的選址疑慮，七股居民與環團草擬「環境與社會檢核」（以下簡稱「環社檢核」），檢核項目包括環境衝擊、社會影響、民眾參與及資訊公開等部分，供政府與業者參考。經過民間與經濟部的磋商後，今年四月，環社檢核 1.0 版本出爐。 ")]),_c('p',{staticClass:"paragraph"},[_vm._v(" 環社檢核將被納入光電專區審核流程，楊惠欽解釋，環團力求能藉此篩出低影響、適建光電的區位，成為示範案場。「讓沒有爭議的（地方）先做，對地面型的推展速度才會快。」他表示，環團也盼望綠電產量提升，但仍須要求政府更細緻地規範選址，並重視地方公民的參與。 ")]),_c('p',{staticClass:"paragraph"},[_vm._v(" 楊惠欽強調，環社檢核也能讓光電業者不踩雷區，以免投資與整合完成，卻發現案場有種電疑慮。業者為了要符合環社檢核的項目，必得對選址有更多元的人文、生態面考量，避免七股的困境在他處重演。 ")]),_c('hr',{staticClass:"article article-line-through"})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"content"},[_c('h1',{staticClass:"title font-black font-sans"},[_vm._v(" 地面型光電選址怎麼做？ ")]),_c('p',{staticClass:"paragraph"},[_vm._v(" 近幾年，地面型光電面臨諸多爭議，使設置腳步緩慢。設施究竟蓋在哪裡好？我們統整地球公民基金會與政大地政學系助理教授戴秀雄的建議，形成一套選址評估的機制。透過這些步驟，盼能在推動太陽光電與能源轉型的同時，找到適合的區位，讓光電業者不碰壁，也能減少案場的環境與社會破壞，達成綠能臺灣的目標。 ")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"spotlight spotlight-reader"},[_c('p',{staticClass:"paragraph"},[_vm._v(" 農委會於 109 年公布「較無社會及生態疑慮之漁電共生土地初步盤點」"),_c('a',{staticClass:"article article-footnote",attrs:{"title":"農委會盤點全國養殖區，並套疊11類生態敏感圖資及合法、無租賃關係的土地。"}},[_vm._v("註6")]),_vm._v("，顯示七股僅有2公頃土地較適合作為漁電共生案場。然而，早在選址的規劃盤點前，七股的案場申請面積已近450公頃。不只是七股，臺東知本濕地、桃園埤塘、嘉義布袋鹽田等地的光電計畫也因選址疑慮產生不少爭議。 ")]),_c('p',{staticClass:"paragraph"},[_vm._v(" 政府欲發展再生能源，並採友善環境的方式發電；七股居民殷切期盼能守護生態與人文特色。雙方共同在作一個永續臺灣的夢，卻走上分歧的道路。七股敲響能源轉型政策的警鐘——綠電希望守護環境，但若推行不慎，可能成為破壞地方環境的雙面刃。政府應加緊腳步改善當前的規範與流程，別讓漁電共生淪為空談。 ")])])}]

